<template>
  <div class="cus-box">
    <div class="cus-pic row clearfix">
      <img v-for="pic in pics" class="pull-left" :src="pic" alt="新闻轮播图" />
    </div>
    <ul class="cus-ctrls">
      <li :class="{active: idx === 0}"></li>
      <li :class="{active: idx === 1}"></li>
      <li :class="{active: idx === 2}"></li>
      <li :class="{active: idx === 3}"></li>
      <li :class="{active: idx === 4}"></li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: 'NewsPicture',
    data () {
      return {
        pics: ['/zhihudaily/vue-zhihudaily/static/img/top1.jpg', '/zhihudaily/vue-zhihudaily/static/img/top2.jpg', '/zhihudaily/vue-zhihudaily/static/img/top3.jpg', '/zhihudaily/vue-zhihudaily/static/img/top4.jpg', '/zhihudaily/vue-zhihudaily/static/img/top5.jpg'],
        idx: 0
      }
    },
    methods: {
      startTimer () {
        setInterval(() => {
          this.idx++
          this.idx %= 5
        }, 2000)
      }
    },
    created () {
      this.startTimer()
    }
  }
</script>

<style scoped>
  .cus-box{
    position: relative;
    width: 100%;
    overflow-x: hidden;
  }
  .cus-pic{
    width: 500%;
    margin: 0;
    animation: carousel 10s linear infinite;
    animation-delay: 1s;
  }
  .cus-pic img{
    width: 20%;
    height: 235px;
  }
  .cus-ctrls{
    position: absolute;
    bottom: 10px;
    left: 50%;
    width: 120px;
    padding: 0;
    margin-left: -40px;
  }
  .cus-ctrls li{
    float: left;
    width: 10px;
    height: 10px;
    background-color: #fff;
    margin-right: 5px;
    border-radius: 50%;
    list-style: none;
  }
  .cus-ctrls li.active{
    background-color: #666;
  }

  /* 轮播图动画 */
  @-webkit-keyframes carousel{
    0%, 100%{
      transform: translateX(0);
    }
    20%{
      transform: translateX(-20%);
    }
    40%{
      transform: translateX(-40%);
    }
    60%{
      transform: translateX(-60%);
    }
    80%{
      transform: translateX(-80%);
    }
  }

  @keyframes carousel{
    0%, 100%{
      transform: translateX(0);
    }
    20%{
      transform: translateX(-20%);
    }
    40%{
      transform: translateX(-40%);
    }
    60%{
      transform: translateX(-60%);
    }
    80%{
      transform: translateX(-80%);
    }
  }

    /* 媒体查询 */
  @media screen and (min-width: 768px) {
    .cus-box{
      width: 640px;
      margin: 0 auto;
      overflow: hidden;
    }
    .cus-pic{
      height: auto;
    }
  }
</style>



