<template>
  <div class="cus-me container-fluid">
    <div class="cus-about">
      <p>本项目旨在学习vue框架,使用了</p>
      <p>vue-cli脚手架</p>
      <p>vuex&nbsp;状态管理</p>
      <p>vue-router&nbsp;路由管理</p>
      <p>vue-resource&nbsp;资源请求</p>
      <p>知乎API&nbsp;提供数据</p>
      <p>vue-devtools&nbsp;调试代码</p>
    </div>
    <div class="cus-box">
      <p><i class="fa fa-user"></i>&emsp;ntnyq</p>
      <p><i class="fa fa-wechat"></i>&emsp;ntn_yq</p>
      <p><i class="fa fa-github"></i>&emsp;<a href="https://github.com/ntnyq" target="_blank">ntnyq</a></p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'AboutMe',
    data () {
      return {

      }
    }
  }
</script>

<style scoped>

.cus-about{
  padding: 20px 0 0 0;
  font-size: 18px;
  color: #3ba776;
  text-align: center;
}
.cus-about p{
  line-height: 1.3;
}
.cus-box, .cus-box::before, .cus-box::after{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.cus-box{
  width: 200px;
  height: 200px;
  margin: auto;
  margin-bottom: 20px;
  padding: 40px 0 0 30px;
  color: #222;
  font-size: 24px;
  box-shadow: inset 0 0 0  1px #666;
}

/* 点击下划线效果 */
.cus-box a{
  position: relative;
  display: inline-block;
  color: #3ba776;
}
.cus-box a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #3ba776;
  visibility: hidden;
  transform: scaleX(0);
  transition: all .4s ease-in-out;
}
.cus-box a:hover {
  text-decoration: none;
}
.cus-box a:hover::after,
.cus-box a:active::after {
  visibility: visible;
  transform: scaleX(1);
}
.cus-box i{
  width: 30px;
  text-align: center;
}
.cus-box p:nth-of-type(1) i{
  color: blue;
}
.cus-box p:nth-of-type(2) i{
  color: green;
}
.cus-box p:nth-of-type(3) i{
  color: indigo;
}
.cus-box::before, .cus-box::after{
  content: '';
  z-index: -1;
  margin: -5%;
  box-shadow: inset 0 0 0 2px #3ba776;
  animation: clipMe 8s linear infinite;
}
.cus-box::before{
  animation-delay: -4s;
}

@-webkit-keyframes clipMe{
  0%, 100% {
        clip: rect(0px, 220.0px, 2px, 0px);
  }
  25% {
        clip: rect(0px, 2px, 220.0px, 0px);
  }
  50% {
        clip: rect(218.0px, 220.0px, 220.0px, 0px);
  }
  75% {
        clip: rect(0px, 220.0px, 220.0px, 218.0px);
  }
}

@keyframes clipMe{
  0%, 100% {
        clip: rect(0px, 220.0px, 2px, 0px);
  }
  25% {
        clip: rect(0px, 2px, 220.0px, 0px);
  }
  50% {
        clip: rect(218.0px, 220.0px, 220.0px, 0px);
  }
  75% {
        clip: rect(0px, 220.0px, 220.0px, 218.0px);
  }
}
</style>



