<template>
  <div class="container-fluid">
    <div @click="closeShareBox" class="cus-panel panel panel-info">
      <div class="panel-heading">
        <h2 class="panel-title text-center">分&emsp;享</h2>
      </div>
      <div class="cus-body panel-body">
        <div class="cus-item1 row">
          <div @click.stop="showQRCode" class="col-xs-4">
            <a class="text-center" href="javascript:;"><i class="fa fa-wechat"></i></a>
            <p class="text-center">微信</p>
          </div>
          <div class="col-xs-4">
            <a class="text-center" href="http://v.t.sina.com.cn/share/share.php?url=https://ntnyq.github.io&title='我的个人博客'" target="_blank"><i class="fa fa-weibo"></i></a>
            <p class="text-center">微博</p>
          </div>
          <div class="col-xs-4">
            <a class="text-center" href=""><i class="fa fa-qq"></i></a>
            <p class="text-center">QQ</p>
          </div>
        </div>
        <div class="cus-item2 row">
          <div class="col-xs-4">
            <a class="text-center" href=""><i class="fa fa-tencent-weibo"></i></a>
            <p class="text-center">腾讯微博</p>
          </div>
          <div class="col-xs-4">
            <a class="text-center" href=""><i class="fa fa-pencil"></i></a>
            <p class="text-center">有道云</p>
          </div>
          <div class="col-xs-4">
            <a class="text-center" href=""><i class="fa fa-github"></i></a>
            <p class="text-center">Github</p>
          </div>
        </div>
        <div class="cus-item3 row">
          <div class="col-xs-4">
            <a class="text-center" href=""><i class="fa fa-ellipsis-h"></i></a>
            <p class="text-center">更多</p>
          </div>
        </div>
      </div>
    </div>
    <div class="mask"></div>
  </div>
</template>

<script>
  export default {
    name: 'ShareBox',
    data () {
      return {

      }
    },
    computed: {
      curNews () {
        return this.$store.state.curNews
      }
    },
    methods: {
      closeShareBox () { // 主动路由跳转
        this.$router.push({name: 'article'}) // 注意这里是router $route表示当前路由对象 $router表示 全局的路由对象
      },
      showQRCode () { // 注意停止事件的冒泡
        this.$router.push({name: 'article'})
        this.$store.dispatch('toggleQRCodeShow')
      }
    }
  }
</script>

<style scoped>
  .cus-panel{
    position: fixed;
    left: 50%;
    top: 20%;
    z-index: 88;
    width: 300px;
    height: 350px;
    margin-left: -150px;
  }
  .cus-panel h2{
    font-size: 24px;
  }
  .cus-panel a{
    display: block;
    width: 60px;
    height: 60px;
    line-height: 60px;
    margin: 0 auto 5px;
    border-radius: 50%;
    background-color: #ccc;
  }
  .cus-item1 div:nth-of-type(1) a{
    background-color: #0f0;
  }
  .cus-item1 div:nth-of-type(2) a{
    background-color: #f00;
  }
  .cus-item1 div:nth-of-type(3) a{
    background-color: #1c80d2;
  }
  .cus-item2 div:nth-of-type(1) a{
    background-color: #41a3ee;
  }
  .cus-item2 div:nth-of-type(2) a{
    background-color: #3994ff;
  }
  .cus-item2 div:nth-of-type(3) a{
    background-color: #6655d5;
  }
  .cus-item3 a{
    background-color: #999;
  }
  .cus-panel i{
    font-size: 24px;
    color: #fff;
  }
  .cus-body{
    background-color: #eee;
  }
  .mask{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 66;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .8);
  }

</style>



