<template>
  <div v-if="!isError" class="cus-detail container-fluid">
    <div class="row">
      <img :src="picUrl" alt="" />
    </div>
    <div v-html="context" class="cus-text container"></div>
    <transition name="show-box">
      <div v-if="isBoxShow" class="cus-tip">
        <i class="fa fa-thumbs-up"></i>&nbsp;{{curNews.likes - 1}} + 1
      </div>
    </transition>
    <router-view></router-view>
    <div v-if="isQRCodeShow" class="cus-img">
      <img src="/zhihudaily/vue-zhihudaily/static/img/wechat.jpg" alt="微信二维码" />
    </div>
  </div>
  <div v-else class="panel panel-default">
    <div class="panel-heading">
      <div class="panel-title">
        <h3 class="h3 text-danger">提示</h3>
      </div>
      <div class="panel-body">
        <p class="text-info">本地版本的数据量有限，目前并没有这篇文章</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'NewsDetail',
    data () {
      return {
        newsid: '',
        context: '',
        picUrl: '',
        isError: false
      }
    },
    computed: {
      isBoxShow () {
        return this.$store.state.isBoxShow
      },
      curNews () {
        return this.$store.state.curNews
      },
      isQRCodeShow () {
        return this.$store.state.isQRCodeShow
      }
    },
    methods: {
      fetchData () {
        this.newsid = this.$route.params.id
        this.$http.get('/zhihudaily/vue-zhihudaily/static/json/' + this.newsid + '.json')
        .then((data) => {
          this.context = data.body.body
          this.picUrl = data.body.image
          // 获取数据是异步的 所以改变状态要在这里改变
          const news = {
            id: parseInt(this.$route.params.id),
            comments: data.body.extra.comments,
            likes: data.body.extra.popularity,
            isLiked: false, // 初始化为false
            isCollected: false
          }
          this.$store.dispatch('storeNewsInfo', news) // 先保存页面信息
          this.setCurrentNews() // 再设置当前页
        }, (err) => {
          console.log(err)
          this.isError = true
        })
      },
      setCurrentNews () {
        if (this.$route.params.id) {
          this.id = this.$route.params.id
          this.$store.dispatch('setCurrentNews', this.id)
        }
      }
    },
    watch: {
      '$route': 'fetchData',
      // '$route': 'setCurrentNews',
      isBoxShow () { // 监听提示框状态变动
        if (this.isBoxShow) { // 提示框显示才1秒后移除它
          setTimeout(() => { // 箭头函数的this指向不变
            this.$store.dispatch('toggleLikedBox')
          }, 600)
        }
      }
    },
    created () {
      this.fetchData()
    }
  }
</script>

<style>
/*   有动态内容的 给style标签价scoped限定后无法覆盖bootstrap样式 */
  .cus-detail img{
    width: 100%;
    height: 235px;
  }
  .cus-detail .avatar{
    width: 21px;
    height: 21px;
  }
  /* 文章内容样式 */
  .question-title{
    font-size: 18px;
    color: #333;
  }
  .meta{
    margin-bottom: 20px;
    width: 280px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
  span.author{
    font-weight: 700;
    color: #444;
  }
  .bio{
    color: #666;
    font-size: 14px;
  }
  .cus-text{
    padding: 0;
  }
  .content p{
    line-height: 1.8;
    margin: 10px 0;
  }
  .content p a{
    color: #000;
  }
  .view-more{
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
    line-height: 40px;
    text-align: center;
    background-color: rgba(0, 0, 0, .3);
  }
  .view-more a{
    color: #666;
  }
  /*点赞弹框样式*/
  .cus-tip{
    position: fixed;
    bottom: 30%;
    left: 50%;
    width: 100px;
    height: 30px;
    line-height: 30px;
    margin-left: -50px;
    background-color: rgba(0, 0, 0, .6);
    border-radius: 5px;
    text-align: center;
    color: #fff;
    transition: all .5s ease;
  }
  /* 展示微信二维码 */
  .cus-img{
    position: fixed;
    left: 50%;
    top: 50%;
    z-index: 88;
    width: 200px;
    height: 200px;
    margin-left: -100px;
    margin-top: -100px;
    border: 2px solid #999;
    padding: 18px;
    background-color: #fff;
    box-sizing: border-box;
  }
  .cus-img img{
    width: 160px;
    height: 160px;
  }

  /* vue动画效果 */
 .show-box-enter-active {
    animation: show-box-in .5s;
  }
  .show-box-leave-active {
    animation: show-box-out .8s;
  }
  @-webkit-keyframes show-box-in {
    0% {
      transform: scale(.4) translateX(300px);
      opacity: 1;
    }
    25% {
      transform: scale(.6) translateX(20px);
    }
    50% {
      transform: scale(1) translateX(-20px);
    }
    75%{
      transform: scale(1.3) translateX(10px);
    }
    100% {
      transform: scale(1);
    }
  }
  @-webkit-keyframes show-box-out {
    0% {
      transform: scale(1.3, .7) translateY(-15px);
    }
    30% {
      transform: scale(1) translateY(15px);
    }
    60% {
      transform: scale(.7, 1.3) translateY(-8px);
    }
    80%{
      transform: scale(1) translateY(8px);
    }
    100% {
      transform: scale(1) translateY(100px);
    }
  }
  @keyframes show-box-in {
    0% {
      transform: scale(.4) translateX(300px);
      opacity: 1;
    }
    25% {
      transform: scale(.6) translateX(20px);
    }
    50% {
      transform: scale(1) translateX(-20px);
    }
    75%{
      transform: scale(1.3) translateX(10px);
    }
    100% {
      transform: scale(1);
    }
  }
  @keyframes show-box-out {
    0% {
      transform: scale(1.3, .7) translateY(-10px);
    }
    30% {
      transform: scale(1) translateY(20px);
    }
    60% {
      transform: scale(.7, 1.3) translateY(-20px);
    }
    80%{
      transform: scale(1) translateY(10px);
    }
    100% {
      transform: scale(1) translateY(200px);
    }
  }

  /* 媒体查询 */
  @media screen and (min-width: 768px) {
    .cus-detail{
      width: 640px;
      margin: 0 auto;
    }
    .cus-text{
      width: 640px;
    }
  }
</style>



