<template>
  <div v-if="!isError" class="cus-column container-fluid">
    <div class="jumbotron">
      <h2 class="h2"><i class="fa fa-tags"></i>&emsp;{{ columnInfo.name }}</h2>
      <small><i class="fa fa-quote-left"></i> {{ columnInfo.description }} <i class="fa fa-quote-right"></i></small>
    </div>
    <div class="cus-table table table-hover">
      <caption class="text-center text-info">主编介绍</caption>
      <thead>
        <tr>
          <th>昵称&emsp;<i class="fa fa-user-o"></i></th>
          <th>签名&emsp;<i class="fa fa-paper-plane-o"></i></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="info in columnInfo.editors">
          <td>{{ info.name }}</td>
          <td>{{ info.bio }}</td>
        </tr>
      </tbody>
    </div>
    <div class="list-group">
      <div @click="startShake" v-for="news in columnNews" :class="{shake: isShake}" :id="news.id" class="cus-item list-group-item">
        <h3><i class="fa fa-volume-down"></i>&emsp;{{ news.title }}</h3>
      </div>
    </div>
  </div>
  <div v-else class="panel panel-danger">
    <div class="panel-heading">
      <div class="panel-title">
        <h2>抱歉</h2>
      </div>
    </div>
    <div class="panel-body">
      本地数据有限，可能未保存此项目的json文件
    </div>
  </div>
</template>

<script>
  export default {
    name: 'NewsColumn',
    data () {
      return {
        columnId: '',
        columnNews: [],
        columnInfo: {},
        isShake: false,
        isError: false
      }
    },
    methods: {
      fetchData () {
        this.columnId = this.$route.params.id
        this.$http.get('/zhihudaily/vue-zhihudaily/static/json/' + this.columnId + '.json')
        .then((data) => {
          this.columnNews = data.body.stories
          this.columnInfo = {
            description: data.body.description,
            name: data.body.name,
            editors: data.body.editors
          }
          this.$store.dispatch('setColumnTitle', this.columnInfo.name)
        }, (err) => {
          console.log(err)
          this.isError = true
        })
      },
      startShake () {
        this.isShake = true
        setTimeout(() => {
          this.isShake = false
        }, 5000)
      }
    },
    created () {
      this.fetchData()
    }
  }
</script>

<style scoped>
  .cus-column{
    background-color: #eee;
  }
  .cus-table caption{
    width: 320px;
    margin: -10px auto 0;
    font-size: 20px;
    color: #41b883;
  }
  .cus-table td{
    color: #666;
  }
  .cus-item{
    margin-bottom: 10px;
    box-shadow: 0px 3px 8px #999;
  }
  .cus-item.shake{
    animation: shake 2s ease-in-out infinite;
  }
  .cus-item h3{
    font-size: 16px;
    color: #666;
  }

  @-webkit-keyframes shake{
    0%{
      transform: translateX(-2px);
    }
    10%{
      transform: translateX(3px);
    }
    20%{
      transform: translateX(-4px);
    }
    30%{
      transform: translateX(5px);
    }
    40%{
      transform: translateX(-6px);
    }
    50%{
      transform: translateX(-5px);
    }
    60%{
      transform: translateX(4px);
    }
    70%{
      transform: translateX(-3px);
    }
    80%{
      transform: translateX(2px);
    }
    90%{
      transform: translateX(-1px);
    }
    100%{
      transform: translateX(0);
    }
  }

  @keyframes shake{
    0%{
      transform: translateX(-2px);
    }
    10%{
      transform: translateX(3px);
    }
    20%{
      transform: translateX(-4px);
    }
    30%{
      transform: translateX(5px);
    }
    40%{
      transform: translateX(-6px);
    }
    50%{
      transform: translateX(-5px);
    }
    60%{
      transform: translateX(4px);
    }
    70%{
      transform: translateX(-3px);
    }
    80%{
      transform: translateX(2px);
    }
    90%{
      transform: translateX(-1px);
    }
    100%{
      transform: translateX(0);
    }
  }

  /* 媒体查询 */
  @media screen and (min-width: 768px) {
    .cus-column{
      width: 640px;
      margin: 0 auto;
    }
  }
</style>



