<template>
  <div v-if="isShow" class="cus-loading">
    <span>{{ text }}</span>
    <em>{{ copy }}</em>
  </div>
</template>

<script>

  export default {
    name: 'AppLoading',
    data () {
      return {
        text: '© Fido Dido',
        copy: 'By ntnyq'
      }
    },
    props: {
      isShow: {
        type: Boolean,
        required: true
      }
    }
  }

</script>

<style scoped>
  .cus-loading{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 18888;
    width: 100%;
    height: 100%;
    background: url(/zhihudaily/vue-zhihudaily/static/img/loading.jpg) no-repeat;
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    overflow: hidden;
  }
  .cus-loading span,
  .cus-loading em{
    position: absolute;
    bottom: 5%;
    font-size: 16px;
    color: #fff;
  }
  .cus-loading span{
    left: 5%;
  }
  .cus-loading em{
    right: 5%;
    font-style: normal;
  }
</style>



