<template>
  <div class="cus-login container-fluid">
    <h3 class="h3 text-center">知乎日报</h3>
    <form class="form-group" role="form">
      <label for="username">用户名</label>
      <span>{{ usernameTip }}</span>
      <input v-model="username" class="form-control" type="text" placeholder="用户名/手机号/邮箱" />
    </form>
    <form class="form-group" role="form">
      <label for="password">密码</label>
      <span>{{ passwordTip }}</span>
      <input v-model="password" class="form-control" type="password" placeholder="请输入您的密码" />
    </form>
    <div class="cus-btns btn-group">
      <button @click="login" class="btn btn-primary">登&nbsp;录</button>
      <button  @click="registerMember" class="btn btn-info">注&nbsp;册</button>
    </div>
    <p>知乎日报不会未经同意通过您的账号发表任何消息</p>
  </div>
</template>

<script>
  export default {
    name: 'UserLogin',
    data () {
      return {
        username: '',
        password: '',
        usernameTip: '',
        passwordTip: ''
      }
    },
    computed: {
      validateUserName () {
        const userReg = /^[a-zA-Z][a-zA-Z0-9_@.]{5,15}$/
        if (this.username.length < 6 || this.username.length > 16) {
          return '用户名限制为6至16位'
        } else if (!userReg.test(this.username)) {
          return '用户名只能由字母开始'
        }
      },
      validatePassword () {
        const pwdReg = /^[a-zA-Z][(\w)!@#%]{9,15}$/  // \w匹配所有字母数组点和下划线
        if (this.password.length < 10 || this.password.length > 16) {
          return '密码限制为10至16位'
        } else if (!pwdReg.test(this.password)) {
          return '密码必须由字母开始'
        }
      },
      userInfo () {
        return this.$store.state.user
      },
      isUserLogin () {
        return this.$store.state.isUserLogin
      }
    },
    methods: {
      login () {
        if (this.userInfo.name === '' && this.userInfo.password === '') {
          alert('您尚未注册')
        } else {
          if (this.username === this.userInfo.name && this.password === this.userInfo.password) {
            if (!this.isUserLogin) {
              alert('登录成功')
              this.$store.dispatch('toggleLoginState') // 若未登录更改用户状态 不过已经登录就应该不给看这个页面
            }
          } else if (this.username !== this.userInfo.name) {
            this.usernameTip = '用户名不正确' // 这里应该另找个位置提示 用户名或者密码不正确
          } else if (this.password !== this.userInfo.password) {
            this.passwordTip = '密码不正确'
          }
        }
      },
      registerMember () {
        this.usernameTip = this.validateUserName
        this.passwordTip = this.validatePassword
        if (!this.validateUserName && !this.validatePassword) {
          alert('注册成功，可登录')
          const userInfo = {
            name: this.username,
            password: this.password
          }
          this.$store.dispatch('setUserInfo', userInfo) // 注册成功存入vuex
          this.username = '' // 清空输入框
          this.password = ''
        }
      }
    },
    created () {
      document.querySelector('body').style.height = document.querySelector('body').clientHeight
    }
  }
</script>

<style scoped>
  .cus-login{
    position: fixed;
    width: 100%;
    height: 100%;
    background: -webkit-radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    background: -o-radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    background: -moz-radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    background: radial-gradient(left top, #359ae5, #1372ba, #0f5c94);
    padding: 0;
  }
  .cus-login h3{
    margin-top: 100px;
    font-family: "Comic Sans MS",cursive;
    font-size: 32px;
    font-weight: 600;
    color: #fff;
  }
  .cus-login label{
    font-size: 18px;
    font-weight: bold;
    color: #fff;
  }
  .cus-login span{
    margin-left: 10px;
    font-size: 14px;
    font-weight: bold;
    letter-spacing: 1px;
    color: orange;
  }
  .cus-login input[type="text"],
  .cus-login input[type="password"]{
    height: 40px;
    margin-top: 5px;
    font-size: 16px;
  }
  .cus-login form{
    position: absolute;
    width: 240px;
    left: 50%;
    margin-left: -120px;
  }
  .cus-login form:nth-of-type(1){
    bottom: 50%;
  }
  .cus-login form:nth-of-type(2){
    bottom: 35%;
  }
  .cus-login .cus-btns{
    position: absolute;
    left: 50%;
    bottom: 25%;
    width: 260px;
    margin-left: -120px;
  }
  .cus-login button{
    padding: 10px 35px;
    margin: 10px;
    border: 2px solid #aaa;
    font-size: 16px;
  }
  .cus-login p{
    position: absolute;
    bottom: 100px;
    width: 100%;
    font-size: 14px;
    text-align: center;
    color: #fff;
  }
</style>



