<template>
  <div class="cus-side container-fluid">
    <transition name="show">
      <div v-if="isSidebarShow" class="cus-bar">
        <div class="cus-user">
          <div class="row">
            <div class="col-xs-3 center-block">
              <img class="img-circle" src="/zhihudaily/vue-zhihudaily/static/img/avatar.png" alt="头像" />
            </div>
            <div class="col-xs-9">
              <span>小学生</span>
            </div>
          </div>
          <div class="row">
            <div class="col-xs-6 text-center">
              <router-link to="/zhihudaily/vue-zhihudaily/collect"><i class="fa fa-star"></i>我的收藏</router-link>
            </div>
            <div class="col-xs-6 text-center">
              <a href="javascript:;"><i class="fa fa-download"></i>离线下载</a>
            </div>
          </div>
        </div>
        <h3 class="h3">
          <router-link to="/zhihudaily/vue-zhihudaily/"><i class="fa fa-home"></i>首&nbsp;页</router-link>
        </h3>
        <div class="cus-channel">
          <div class="list-group">
            <div v-for="theme in themes" class="cus-item list-group-item">
              <router-link :to="{name: 'column', params: {id: theme.id}}">
                <div class="row">
                  <div class="col-xs-9">
                    <span>{{ theme.name }}</span>
                  </div>
                  <div class="col-xs-3">
                    <i class="fa fa-plus"></i>
                  </div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'NewsSidebar',
    data () {
      return {
        themes: []
      }
    },
    methods: {
      fetchThemes () {
        this.$http.get('/zhihudaily/vue-zhihudaily/static/themes.json').then((data) => {
          this.themes = data.body.others
        }, (err) => {
          console.log(err)
        })
      }
    },
    computed: {
      isSidebarShow () {
        return this.$store.state.isMaskShow
      }
    },
    created () {
      this.fetchThemes()
    }
  }
</script>

<style scoped>
  .cus-side{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    height: 110%;
    padding: 0;
    overflow-y: auto;
  }
  .cus-bar{
    /* 要位移需要宽度 */
    width: 300px;
    height: auto;
    padding: 0;
    background-color: #fff;
    font-size: 18px;
    line-height: 1.42;
    color: #fff;
  }
  .cus-user{
    background-color: #00a2ed;
  }
  .cus-user img{
    width: 40px;
    height: 40px;
  }
  .cus-user>div{
    height: 60px;
    line-height: 50px;
    padding: 10px 20px;
  }
  .cus-user i{
    margin-right: 10px;
  }
  .cus-user a{
    color: #fff;
    text-decoration: none;
  }
  .cus-bar h3{
    color: #00a2ed;
    margin: 0;
    padding: 20px;
    background-color: #f3f3f3;
    font-size: 16px;
  }
  .cus-bar h3 a{
    text-decoration: none;
  }
  .cus-bar h3 i{
    margin-right: 20px;
  }
  .cus-channel{
    margin-bottom: 50px;
  }
  .cus-channel span{
    margin-left: 10px;
    font-size: 16px;
    color: #222;
  }
  .cus-channel i{
    color: #bbb;
  }

/* vue的过渡动画 */
  .show-enter-active,
  .show-leave-active{
    transition: all .3s ease;
  }
  .show-enter, .show-leave-active {
    transform: translateX(-100%);
  }

  /* 夜间模式 */
  .night .cus-user{
    background-color: #222;
  }
  .night .cus-user a,
  .night .cus-user span{
    color: #999;
  }
  .night .cus-user i{
    color: #fff;
  }
  .night .cus-bar h3{
    background-color: #333;
  }
  .night .cus-channel,
  .night .cus-item{
    background-color: #555;
    border: none;
  }
  .night .cus-channel span{
    color: #aaa;
    font-weight: 400;
  }

 /* 隐藏滚动条 */
  ::-webkit-scrollbar{
    background-color: transparent;
  }

</style>



