<template>
  <div :class="{night: isNightPattern}" @click="closeBox" class="cus-wrapper container-fluid">
    <news-header></news-header>
    <router-view></router-view>
  </div>
</template>

<script>
  import NewsHeader from './components/NewsHeader.vue'

  export default {
    name: 'App',
    components: {
      NewsHeader
    },
    computed: {
      isNightPattern () {
        return this.$store.state.isNightPattern
      },
      isCtrlShow () {
        return this.$store.state.isCtrlShow
      },
      isQRCodeShow () {
        return this.$store.state.isQRCodeShow
      }
    },
    methods: {
      closeBox () {
        if (this.isCtrlShow) { // 只有设置控制框显示 才可点击消失
          this.$store.dispatch('toggleCtrlBox')
        }
        if (this.isQRCodeShow) {
          this.$store.dispatch('toggleQRCodeShow')
        }
      }
    }
  }
</script>

<style>
  *{
    font-family: Microsoft Yahei, Arial;
  }
  .cus-wrapper{
    width: 100%;
    overflow-x: hidden;
    padding: 0;
    padding-top: 50px;
  }
  img, a{
    border: none;
  }

  /* 移动端美化样式 */
  input, a, button{
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  body{
    -webkit-touch-callout:none;
    -webkit-overflow-scrolling: touch;
  }

  @media screen and (min-width: 768px) {
    .cus-wrapper{
      padding-top: 80px;
    }
  }
</style>



