<template>
  <div class="cus-more container-fluid">
    <div class="list-group">
      <h2 class="text-center">{{ formatDate }}</h2>
      <div v-for="msg in news" class="col-md-5 col-md-push-1 col-xs-12 cus-item list-group-item">
      <!-- 注意to前面的冒号 更多参考vue docs 动态路由 命名路由 -->
        <router-link :to="{name: 'article', params: {id: msg.id}}">
          <div class="row">
            <div class="col-xs-8">
              <h3>{{ msg.title }}</h3>
            </div>
            <div class="col-xs-4">
              <img :src="msg.images[0]" alt="" />
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'NewsLoadMore',
    data () {
      return {
        date: '',
        news: [],
        times: 0
      }
    },
    props: { // 父组件传过来的值 要获取的新闻日期
      newsIndex: {
        type: Number,
        required: true
      }
    },
    computed: {
      // 格式化日期
      formatDate () {
        let temp = this.date.split('')
        temp.splice(4, 0, '/')
        temp.splice(7, 0, '/')
        return this.date.substring(4, 6) + '月' + this.date.substring(6, 8) + '日 星期' + '日一二三四五六'.charAt(new Date(temp.join('')).getDay())
      },
      isMoreShow () {
        return this.$store.state.isMoreShow
      }
    },
    methods: {
      fetchData () {
        if (this.isMoreShow) { // 只有在可以继续加载的情况下才加载文件
          this.$http.get('/zhihudaily/vue-zhihudaily/static/' + this.newsIndex + '.json').then((data) => {
            this.news = data.body.stories
            this.date = data.body.date
            this.$store.dispatch('stopLoadMore')
          }, (err) => {
            console.log(err)
          })
        }
      }
    },
    created () {
      this.fetchData()
    }
  }
</script>

<style scoped>
.cus-more{
  background-color: #f3f3f3;
}
.cus-more h2{
  padding: 5px;
  font-size: 14px;
  color: #6c6c6c;
}
.cus-more h3{
  font-size: 16px;
  color: #2b2b2b;
  line-height: 1.42;
}
.cus-more img {
  width: 86px;
  height: 88px;
}
.cus-item{
  height: 120px;
  margin-bottom: 10px;
  box-shadow: 0px 3px 8px #ccc;
}

/* 夜间模式 */
.night .cus-more{
  background-color: #444;
}
.night .cus-more h2{
  color: #bbb;
}
.night .cus-more .cus-item{
  background-color: #555;
  border: none;
  box-shadow: none;
}
.night .cus-more h3{
  color: #eee;
}
</style>



