<template>
  <div class="cus-article container-fluid">
    <div class="list-group">
      <h2 class="text-center">今日新闻</h2>
      <div v-for="msg in news" class="col-md-5 col-md-push-1 col-xs-12 cus-item list-group-item">
      <!-- 注意to前面的冒号 更多参考vue docs 动态路由 命名路由 -->
        <router-link :to="{name: 'article', params: {id: msg.id}}">
          <div class="row">
            <div class="col-xs-8">
              <h3>{{ msg.title }}</h3>
            </div>
            <div class="col-xs-4">
              <img :src="msg.images[0]" alt="" />
            </div>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'NewsArticle',
    data () {
      return {
        news: []
      }
    },
    methods: {
      fetchData () {
        this.$http.get('/zhihudaily/vue-zhihudaily/static/20170308.json').then((data) => {
          this.news = data.body.stories
        }, (err) => {
          console.log(err)
        })
      }
    },
    created () {
      this.fetchData()
    }
  }
</script>

<style scoped>
  .cus-article{
    background-color: #f3f3f3;
  }
  .cus-article h2{
    font-size: 14px;
    color: #6c6c6c;
  }
  .cus-article h3{
    padding: 5px;
    font-size: 16px;
    color: #2b2b2b;
    line-height: 1.42;
  }
  .cus-article img {
    width: 86px;
    height: 88px;
  }
  .cus-item{
    height: 120px;
    margin-bottom: 10px;
    box-shadow: 0px 3px 8px #ccc;
  }

  /* 夜间模式 */
  .night .cus-article{
    background-color: #444;
  }
  .night .cus-article h2{
    color: #bbb;
  }
  .night .cus-article .cus-item{
    background-color: #555;
    border: none;
    box-shadow: none;
  }
  .night .cus-article h3{
    color: #eee;
  }
</style>



