<template>
<div class="cus-home container-fluid">
  <news-picture></news-picture>
  <news-article></news-article>
  <news-sidebar></news-sidebar>
  <!-- 动态添加组件 页面每到底部 又添一条 -->
  <news-load-more v-for=" n in num" :key="n" :newsIndex="startDate - n + 1"></news-load-more>
  <div v-if="num === limit" class="cus-info">
    <p>抱歉，没有资源可以加载了</p>
  </div>
  <div @click="toggleSidebar" v-if="isMaskShow" class="mask"></div>
  <app-loading :isShow="isLoadingShow"></app-loading>
</div>
</template>

<script>
  import NewsPicture from './NewsPicture.vue'
  import NewsSidebar from './NewsSidebar.vue'
  import NewsArticle from './NewsArticle.vue'
  import NewsLoadMore from './NewsLoadMore.vue'
  import AppLoading from './AppLoading.vue'

  export default {
    name: 'NewsHome',
    components: {
      NewsPicture,
      NewsSidebar,
      NewsArticle,
      NewsLoadMore,
      AppLoading
    },
    data () {
      return {
        num: 0,
        startDate: 20170306,
        limit: 4,  // 本地环境 按文件名取资源 资源有限
        isLoadingShow: true
      }
    },
    methods: {
      toggleSidebar () {
        this.$store.dispatch('toggleSidebar')
      },
      handleScroll () {
        if (this.$route.path === '/zhihudaily/vue-zhihudaily/' || this.$route.path === '/zhihudaily/vue-zhihudaily/home') {
          let home = document.querySelector('.cus-home')
          if (home.clientHeight + home.offsetTop - 50 < window.scrollY + document.documentElement.clientHeight) {
            if (this.num < this.limit) {
              this.num++
              if (!this.isMoreShow) { // 只有在可以关闭的时候才关闭这个控制
                this.$store.dispatch('continueLoadMore') // 控制是否继续加载 全局
              }
            }
          }
        }
      }
    },
    computed: {
      isMaskShow () {
        return this.$store.state.isMaskShow
      },
      isMoreShow () {
        return this.$store.state.isMoreShow
      }
    },
    created () { // 生命周期 属性已经绑定 DOM未生成

    },
    watch: {
      '&route': 'handleScroll'
    },
    mounted () {  // 模板编译挂载之后
      if (this.$route.path === '/zhihudaily/vue-zhihudaily/') {
        window.addEventListener('scroll', this.handleScroll, false)
      }
      setTimeout(() => {
        this.isLoadingShow = false
      }, 300)
    }
  }
</script>

<style scoped>
  .cus-home{
    padding: 0;
  }
  .cus-info{
    background-color: #eee;
    padding: 10px 0;
    color: #41b883;
    font-size: 20px;
    text-align: center;
  }
  .mask{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 8888;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .6);
  }

  /* 夜间模式 */
  .night .cus-info{
    background-color: #222;
  }
</style>



