<template>
  <div class="cus-box container-fluid">
    <div class="cus-group list-group">
      <h3 class="h3">常规</h3>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">自动离线下载</h4>
            <span>仅wifi下可用，自动下载最新内容</span>
          </div>
          <div class="cus-check col-xs-3">
            <input id="check1" type="checkbox" value="1" v-model="checkedList" />
            <label @click="" for="check1"></label>
          </div>
        </div>
      </div>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">无图模式</h4>
            <span>2G/3G网络下不下载图片</span>
          </div>
          <div class="cus-check col-xs-3">
            <input id="check2" type="checkbox" value="2" v-model="checkedList" />
            <label for="check2"></label>
          </div>
        </div>
      </div>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">大字号</h4>
          </div>
          <div class="cus-check col-xs-3">
            <input id="check3" type="checkbox" value="3" v-model="checkedList" />
            <label for="check3"></label>
          </div>
        </div>
      </div>
    </div>
    <div class="cus-group list-group">
      <h3 class="h3">其他</h3>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">推送消息</h4>
          </div>
          <div class="cus-check col-xs-3">
            <input id="check4" type="checkbox" value="4" v-model="checkedList" />
            <label for="check4"></label>
          </div>
        </div>
      </div>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">点评分享到微博</h4>
          </div>
          <div class="cus-check col-xs-3">
            <input id="check5" type="checkbox" value="5" v-model="checkedList" />
            <label for="check5"></label>
          </div>
        </div>
      </div>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">清除缓存</h4>
          </div>
        </div>
      </div>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">检查更新</h4>
            <span>版本：2.6.2(763)</span>
          </div>
        </div>
      </div>
    </div>
    <div class="cus-group list-group">
      <h3 class="h3">反馈</h3>
      <div class="list-group-item">
        <div class="row">
          <div class="col-xs-9">
            <h4 class="h4">意见反馈</h4>
          </div>
        </div>
      </div>
      <div class="list-group-item">
        <div class="cus-about row">
          <div class="col-xs-12 text-center">
            <router-link to="/zhihudaily/vue-zhihudaily/aboutme">关于我</router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'NewsSetting',
    data () {
      return {
        checkedList: []
      }
    },
    // 摧毁生命周期钩子 离开前触发
    beforeDestroy () {
      if (this.checkedList.length > 0) {
        alert('被选中的项有: ' + this.checkedList.sort(function (a, b) {
          return a - b
        }))
      }
    }
  }
</script>

<style scoped>
  .cus-group{
    line-height: 1.42;
  }
  .cus-group h3{
    font-size: 18px;
    color: #008e7f;
  }
  .cus-group h4{
    font-size: 18px;
  }
  .cus-group span{
    color: #828282;
  }
  input[type="checkbox"]{
    width: 20px;
    height: 20px;
    display: none;
  }
  label{
    position: absolute;
    top: 50%;
    left: 50%;
    display: block;
    margin-top: 10px;
    width: 20px;
    height: 20px;
    border: 2px solid #666;
    cursor: pointer;
  }
  label:after{
    content: "";
    position: absolute;
    left: 5px;
    width: 6px;
    height: 14px;
    background-color: transparent;
    border: 3px solid #fff;
    border-top: none;
    border-left: none;
    transform: rotate(45deg);
    opacity: 0;
    filter: alpha(opacity: 0;)
  }
  input[type="checkbox"]:checked+label{
    background-color: #009688;
    border-color: #009688;
  }
  input[type="checkbox"]:checked+label:after{
    opacity: 1;
    filter: alpha(opacity: 100;)
  }
  .cus-check{
    position: relative;
  }
  .cus-about{
    background-color: #3ba776;
    padding: 10px 0;
    border-radius: 10px;
    box-shadow: 5px 5px 5px #ddd;
  }
  .cus-about a{
    font-size: 24px;
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
    letter-spacing: 5px;
    color: #fff;
  }

  /* 媒体查询 */
  @media screen and (min-width: 768px) {
    .cus-group{
      width: 640px;
      margin: 0 auto;
    }
  }
</style>



