import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

// 安装Vuex
Vue.use(Vuex)

// 定义state保存信息
const state = {
  curNews: {
    id: '',
    likes: '',
    comments: '',
    isLiked: '',
    isCollected: ''
  },
  // 登录用户信息
  user: {
    name: '',
    password: ''
  },

  columnTitle: '', // 进入栏目页后的标题

  news: [], // 访问过的新闻列表
  collectList: [], // 收藏的新闻列表

  /* 逻辑量 */
  isMaskShow: false, // 控制是否显示mask
  isNightPattern: false, // 是否夜间模式
  isMoreShow: false, // 是否继续加载
  isBoxShow: false, // 是否显示点赞后的提醒弹窗
  isCtrlShow: false, // 是否显示设置框
  isUserLogin: false, // 用户是否登录
  isQRCodeShow: false // 二维码图片显示
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})



